import React from "react";
import { useState } from "react";
import { useEffect } from "react";
import Fit from "../../fitter/src/Fit";
import { useAlwaysActivationInterval } from "../../services/intervalHooks";
import Simulator from "../FitCard/Stats/services/Simulator";
import ModuleButton from "./ModuleButton";

export default function ContorlPanel(props) {
  const [modules, setModules] = useState();

  useEffect(() => {
    if (!!props?.summarizedSlots?.highSlots) {
      const example = Fit.mapSlots(
        props.summarizedSlots,
        (slot) => {
          if (!slot?.summary?.activationInfo) return false;
          else return slot;
        },
        {
          isIterate: {
            highSlots: true,
            midSlots: true,
            lowSlots: true,
            droneSlots: true,
          },
        }
      )
        .filter((slot) => !!slot)
        .reduce((acc, slot) => {
          if (slot.summary.operation === "damage") {
            const matchingSlotSet = acc.find(
              (slotSet) =>
                slotSet?.[0].item.typeName === slot.item.typeName &&
                slotSet?.[0].charge.typeName === slot.charge.typeName
            );
            if (!matchingSlotSet) acc.push([slot]);
            else matchingSlotSet.push(slot);
          } else acc.push([slot]);

          return acc;
        }, []);
      setModules(example);
    }
  }, [props.updateFlag]);

  useAlwaysActivationInterval(
    () => {
      Simulator.simulate_capacitor(props.summarizedSlots);
      Simulator.simulate_shield(props.summarizedSlots);
      props.setSummarizedSlots({ ...props.summarizedSlots });
    },
    !!props?.summarizedSlots?.summary ? 1000 : null
  );

  return (
    <React.Fragment>
      {modules?.map((moduleSet) => {
        return (
          <ModuleButton
            key={moduleSet[0].summary.path}
            moduleSet={moduleSet}
            summarizedSlots={props.summarizedSlots}
            setSummarizedSlots={props.setSummarizedSlots}
            targetSummarizedSlots={props.targetSummarizedSlots}
          />
        );
      })}
    </React.Fragment>
  );
}
