import { Avatar, Button, makeStyles } from "@material-ui/core";
import React from "react";
import { useState } from "react";
import ModuleButtonChargeBadge from "./ModuleButtonChargeBadge";
import ModuleActivation from "./ModuleActivation";

const useStyles = makeStyles((theme) => ({
  rootDiv: {
    position: "absolute",
    left: 0,
    top: 0,
    width: 40,
    height: 40,
  },
  rootAvatarDefault: {
    width: 40,
    height: 40,
    marginLeft: 10,
    marginTop: 4,
    backgroundColor: theme.palette.background.paper,
    border: `0.1px solid ${theme.palette.divider}`,
    marginRight: 20,
  },
  stateArcBorder: {
    width: 40,
    height: 40,
    borderRadius: "50%",
    border: "3px solid",
    borderLeftColor: "transparent",
    borderRightColor: "transparent",
    borderTopColor: "transparent",
    borderBottomColor: "transparent",
    position: "absolute",
    top: 2,
    left: 7,
  },
  circularProrgess: {
    position: "absolute",
    right: 16,
  },
  circularTransition: {
    transition: theme.transitions.create("stroke-dashoffset", {
      easing: "linear",
      duration: "1s",
    }),
  },
}));

export default function ModuleButton(props) {
  const classes = useStyles();

  const [, render] = useState();

  return (
    <div style={{ position: "relative" }}>
      <ModuleActivation
        summary={props.moduleSet[0].summary}
        moduleSet={props.moduleSet}
        summarizedSlots={props.summarizedSlots}
        setSummarizedSlots={props.setSummarizedSlots}
        targetSummarizedSlots={props.targetSummarizedSlots}
      />
      <ModuleButtonChargeBadge
        count={props.moduleSet[0].summary.activationState.activationLeft}
        onClick={() => {
          activateModuleSet(props.moduleSet, render);
        }}
      >
        <Avatar
          style={{ cursor: "pointer" }}
          onClick={() => {
            activateModuleSet(props.moduleSet, render);
          }}
          className={classes.rootAvatarDefault}
        >
          <Button>
            {feedSource(props.moduleSet[0]?.item, props.moduleSet[0]?.charge)}
          </Button>
        </Avatar>
      </ModuleButtonChargeBadge>
    </div>
  );
}
function feedSource(item, charge) {
  if (!!item && !charge)
    return (
      <img
        draggable="false"
        style={{ width: "85%", height: "85%", marginLeft: -2 }}
        src={`https://images.evetech.net/types/${item?.typeID}/icon?size=64`}
      />
    );
  else if (!!item && !!charge)
    return (
      <img
        draggable="false"
        style={{ width: "85%", height: "85%", marginLeft: -2 }}
        src={`https://images.evetech.net/types/${charge?.typeID}/icon?size=64`}
      />
    );
}
function activateModuleSet(moduleSet, forceRender) {
  // ewww.. mutation
  moduleSet.forEach((module) => {
    module.summary.activationState.isActive =
      !module.summary.activationState.isActive;
  });
  forceRender({});
}
