import { useTheme } from "@material-ui/core";
import React from "react";
import { useState } from "react";
import { useEffect } from "react";
import LinearProgressLabel from "../FitCard/Stats/LinearProgressLabel.jsx";

export default function ShipPanel(props) {
  const theme = useTheme();

  return (
    <React.Fragment>
      <LinearProgressLabel
        value={
          (props.summarizedSlots?.summary.load.shield.HP /
            props.summarizedSlots?.summary.capacity.shield.HP) *
          100
        }
        label={`${props.summarizedSlots?.summary.load.shield.HP.toFixed(2)} / ${
          props.summarizedSlots?.summary.capacity.shield.HP
        }`}
        backgroundColor={theme.palette.property.blueSecondary}
        color={theme.palette.property.blue}
      />
      <LinearProgressLabel
        value={
          (props.summarizedSlots?.summary.load.armor.HP /
            props.summarizedSlots?.summary.capacity.armor.HP) *
          100
        }
        label={`${props.summarizedSlots?.summary.load.armor.HP.toFixed(2)} / ${
          props.summarizedSlots?.summary.capacity.armor.HP
        }`}
        backgroundColor={theme.palette.property.redSecondary}
        color={theme.palette.property.red}
      />
      <LinearProgressLabel
        value={
          (props.summarizedSlots?.summary.load.structure.HP /
            props.summarizedSlots?.summary.capacity.structure.HP) *
          100
        }
        label={`${props.summarizedSlots?.summary.load.structure.HP.toFixed(
          2
        )} / ${props.summarizedSlots?.summary.capacity.structure.HP}`}
        backgroundColor={theme.palette.property.greySecondary}
        color={theme.palette.property.grey}
      />
      <LinearProgressLabel
        value={
          (props.summarizedSlots?.summary.load.capacitor.HP /
            props.summarizedSlots?.summary.capacity.capacitor.HP) *
          100
        }
        label={`${props.summarizedSlots?.summary.load.capacitor.HP.toFixed(
          2
        )} / ${props.summarizedSlots?.summary.capacity.capacitor.HP}`}
        backgroundColor={theme.palette.property.orgSecondary}
        color={theme.palette.property.org}
      />
    </React.Fragment>
  );
}
