import {
  Button,
  ButtonGroup,
  Card,
  Grid,
  makeStyles,
  useTheme,
} from "@material-ui/core";
import SystemUpdateAltIcon from "@material-ui/icons/SystemUpdateAlt";
import RefreshIcon from "@material-ui/icons/Refresh";
import React from "react";
import { useState } from "react";
import Summary from "../FitCard/Stats/services/Summary";
import ContorlPanel from "./ContorlPanel";
import ShipPanel from "./ShipPanel";
import { HAL } from "../FitCard/Stats/services/Simulator";
import { useEffect } from "react";

const useStyles = makeStyles((theme) => ({
  modeButton: {
    backgroundColor: theme.palette.background.paper,
    color: theme.palette.text.primary,
  },
}));

export default function SimulationPanel(props) {
  const classes = useStyles();
  const theme = useTheme();

  const [updateFlag, setUpdateFlag] = useState(false);
  const [summarizedSlots0, setSummarizedSlots0] = useState();
  const [summarizedSlots1, setSummarizedSlots1] = useState();

  return (
    <Card style={{ width: "85%", minWidth: 300, maxWidth: 600 }} elevation={3}>
      <Grid style={{ width: "100%" }}>
        <Grid xs={12} container item justify="center">
          <ButtonGroup
            color="inherit"
            variant="text"
            fullWidth
            disableElevation
          >
            <Button
              className={classes.modeButton}
              onClick={() => {
                const _summarizedSlots0 = Summary.addSummaries(
                  props.slotsSet[0],
                  props.situation.onboard
                );
                const _summarizedSlots1 = Summary.addSummaries(
                  props.slotsSet[1],
                  props.situation.hostile
                );
                HAL.getSchedules_setTarget(
                  _summarizedSlots0,
                  _summarizedSlots1
                );
                HAL.getSchedules_setTarget(
                  _summarizedSlots1,
                  _summarizedSlots0
                );
                setSummarizedSlots0(_summarizedSlots0);
                setSummarizedSlots1(_summarizedSlots1);

                setUpdateFlag(!updateFlag);

                /* console.log(Summary.addSummaries(props.slotsSet[0]));
                console.log(Summary.addSummaries(props.slotsSet[1])); */
              }}
            >
              <SystemUpdateAltIcon
                style={{ color: theme.palette.text.primary }}
              />
            </Button>
            <Button className={classes.modeButton}>
              <RefreshIcon style={{ color: theme.palette.text.primary }} />
            </Button>
          </ButtonGroup>
        </Grid>

        <Grid xs={12} container item justify="center">
          <ShipPanel summarizedSlots={summarizedSlots0} />
        </Grid>
        <Grid xs={12} container item justify="center">
          <ContorlPanel
            summarizedSlots={summarizedSlots0}
            setSummarizedSlots={setSummarizedSlots0}
            targetSummarizedSlots={summarizedSlots1}
            updateFlag={updateFlag}
          />
        </Grid>

        <Grid xs={12} container item justify="center">
          <ShipPanel summarizedSlots={summarizedSlots1} />
        </Grid>
        <Grid xs={12} container item justify="center">
          <ContorlPanel
            summarizedSlots={summarizedSlots1}
            setSummarizedSlots={setSummarizedSlots1}
            targetSummarizedSlots={summarizedSlots0}
            updateFlag={updateFlag}
          />
        </Grid>
      </Grid>
    </Card>
  );
}

function getShipStatusString(summarizedSlots) {
  return `${summarizedSlots?.summary.load.shield.HP}${summarizedSlots?.summary.load.armor.HP}${summarizedSlots?.summary.load.structure.HP}${summarizedSlots?.summary.load.capacitor.HP}`;
}
